  margin: 0;
  padding: 0;
}

body {
  font-family: Cambria, Cochin, Georgia, Times, Times New Roman, serif;
}

header {
  width: 100%;
  background-color: #0ff;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

header .logo {
  align-items: center;
  margin-left: 1em;
  display: flex;
}

header .logo .fa-solid {
  color: red;
  font-size: 3em;
}

header .logo span {
  color: red;
}

header .navigation {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

header .navigation .menu {
  z-index: 1;
  justify-content: center;
  align-items: center;
  transition: all .5s;
  display: flex;
}

header .navigation .menu li {
  list-style-type: none;
}

header .navigation .menu li a {
  color: red;
  text-transform: uppercase;
  padding: 40px 25px;
  font-size: 20px;
  line-height: 1;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

header .navigation .menu li a:hover {
  box-shadow: inset 0 -5px #fff, inset 700px 0 #24222208;
}

header .navigation .hambg {
  width: 30px;
  height: 4px;
  cursor: pointer;
  z-index: 2;
  background: #fff;
  border-radius: 10px;
  transition: all .3s;
  position: relative;
}

header .navigation .hambg:before, header .navigation .hambg:after {
  content: "";
  height: 4px;
  background: #fff;
  border-radius: 10px;
  transition: all .3s;
  position: absolute;
  right: 0;
}

header .navigation .hambg:before {
  width: 20px;
  top: -10px;
}

header .navigation .hambg:after {
  width: 25px;
  top: 10px;
}

header .navigation .toggle-menu {
  width: 30px;
  height: 100%;
  z-index: 3;
  cursor: pointer;
  opacity: 0;
  position: absolute;
}

header .navigation .hambg, header .navigation .toggle-menu {
  display: none;
}

header .navigation input:checked ~ .hambg {
  background: none;
}

header .navigation input:checked ~ .hambg:before {
  width: 30px;
  top: 0;
  transform: rotate(-45deg);
}

header .navigation input:checked ~ .hambg:after {
  width: 30px;
  top: 0;
  transform: rotate(45deg);
}

header .navigation input:checked ~ .menu {
  right: 0;
  box-shadow: -20px 0 40px #0000004d;
}

